}

.nav_list:not(:first-child) {
  margin-left: 20px;
}

.nav_link:hover {
  color: #00acdc;
}

.nav_list a.is-active {
  color: #00acdc;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  display: inline-block;
  position: relative;
}

.nav_list a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
